


















































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Emit,
  Watch,
  Model
} from "vue-property-decorator";
import { getDirectoryContents } from "@/store/api/files";
import {
  IServerResponse,
  IDirectory,
  IDirectoryResponseSuccess
} from "@/shared";
import * as path from "path-browserify";
import uniq from "lodash.uniq";

interface IFileBrowserFileEntry extends IDirectory {
  children?: IFileBrowserFileEntry[];
}

@Component({})
export default class FileBrowser extends Mixins() {
  @Prop({ type: Array, required: true }) value!: string[];

  folders: IFileBrowserFileEntry[] = [];
  isLoading = false;
  tree: string[] = [];
  maxInitialFetchDepth = 2;
  shouldDisplayTreeView = false;
  currentFetchStep = "";

  private processResponse(
    response: IServerResponse<IDirectoryResponseSuccess>
  ): IFileBrowserFileEntry[] {
    if (!response.payload) {
      return [];
    }
    const files = response.payload.folder.map(
      (f): IFileBrowserFileEntry => ({
        ...f,
        children: f.folder ? [] : undefined
      })
    );
    return files;
  }

  async mounted() {
    this.onValueChange();
    this.currentFetchStep = this.$t(
      "file-browser.loading-root-children"
    ).toString();
    const response = await getDirectoryContents({ path: false });
    this.folders = this.processResponse(response);
    this.onValueChange();
    await this.fetchChildrenExistingInModel(this.folders);
    this.shouldDisplayTreeView = true;
  }

  async fetch(entry: IFileBrowserFileEntry): Promise<void> {
    const response = await getDirectoryContents({
      path: entry.fullPath
    });
    entry.children =
      (entry.children && entry.children.length && entry.children) ||
      this.processResponse(response);
  }

  async fetchChildrenExistingInModel(
    entries: IFileBrowserFileEntry[],
    depth = 0
  ) {
    ++depth;
    return Promise.all(
      entries
        .filter(e => e.folder)
        .map(async e => {
          try {
            if (
              this.value.some(folder =>
                path.normalize(folder).startsWith(path.normalize(e.fullPath))
              )
            ) {
              this.currentFetchStep =
                this.$t("file-browser.loading-children-of").toString() +
                " " +
                e.fullPath;
              e.children = this.processResponse(
                await getDirectoryContents({ path: e.fullPath })
              );
              if (depth < this.maxInitialFetchDepth) {
                await this.fetchChildrenExistingInModel(e.children, depth);
              }
            }
          } catch (error) {
            console.log(error);
          }
        })
    );
  }

  async manuallySetFolders(folders: string[]) {
    if (this.shouldDisplayTreeView) {
      this.shouldDisplayTreeView = false;
      this.tree = [...folders];
      this.onValueChange();
      const response = await getDirectoryContents({ path: false });
      this.folders = this.processResponse(response);
      await this.fetchChildrenExistingInModel(this.folders);
      this.shouldDisplayTreeView = true;
    }
  }

  @Emit("input")
  setValue(newVal: this["value"]) {}

  @Watch("folders", { immediate: true, deep: true })
  @Watch("tree")
  updateModel() {
    const dedupedPaths: string[] = [];

    uniq([...this.tree])
      .sort((a, b) => a.length - b.length)
      .forEach(p => {
        if (!dedupedPaths.find(pa => p.startsWith(pa))) {
          dedupedPaths.push(p);
        }
      });

    // // prevent infinite update loop
    let same = dedupedPaths.length === this.value.length;
    dedupedPaths.forEach((t, idx) => {
      if (t !== this.value[idx]) {
        same = false;
      }
    });
    if (!same) {
      this.$nextTick(() => {
        requestAnimationFrame(() => {
          console.log("settings value to", dedupedPaths);
          this.setValue(dedupedPaths);
        }); // todo
      });
    }
  }

  @Watch("folders", { immediate: true, deep: true })
  @Watch("value", { immediate: true, deep: true })
  callOnValueChange() {
    this.tree = this.value;
    this.onValueChange();
  }

  onValueChange() {
    this.maxInitialFetchDepth = Math.max(
      this.maxInitialFetchDepth,
      ...this.value.map(a => a.split(path.sep).length + 1)
    );
  }
}
